import React from 'react';


const Flat = ({
  name,
  imageUrl,
  price,
  priceCurrency,
  lat,
  lng,
  onClick,
  active
}) => {
  const backgroundImage = `url('${imageUrl}')`;
  const classActive = "card card-selected";
  const classInactive = "card";

  return (
    <div className={active ? classActive : classInactive} style={{ backgroundImage }} onClick={() => onClick(lat, lng)}>
      <div className="card-category">
        {price}
        {priceCurrency}
      </div>
      <div className="card-description">
        <h2>{name}</h2>
      </div>

    </div>
  );
};

export default Flat;
