import React from 'react';
import Flat from './Flat';

const FlatList = ({
  flats = [],
  onClick,
  lat,
  lng,
}) => {
  return (
    <div className="flat-list">
      {
        flats.map((flat, index) => (
          <Flat
            onClick={onClick}
            key={index}
            active={flat.lng === lng && flat.lat === lat}
            {...flat}
          />))
      }
    </div>
  );
};

export default FlatList;
